.project-section-bg-img {
  position: absolute;
  width: 100%;
  height: 100%;
  display: none;
  opacity: 0.9;
  z-index: -1;
  transform: translate(-35vw, -5vh);
  filter: drop-shadow(1px 1px 10px black);
}

.cards-main-container {
  position: relative;
  background-color: var(--color-black);
  display: flex;
  justify-content: center;
  z-index: 0;
}

.projects-heading {
  color: var(--color-main);
  width: 100%;
  font-size: 45px;
  text-align: center;
  margin: 25px;
  position: relative;
  font-family: Helvetica, sans-serif;
}
.projects-heading::after {
  content: "";
  width: 30%;
  height: 3px;
  background-color: var(--color-white);
  position: absolute;
  top: 85%;
  left: 0;
  right: 0;
  margin: auto;
  z-index: -1;
}
.cards {
  z-index: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-shrink: 0;
  flex-grow: 0;
  justify-content: center;
  transition: 0s;
  padding-bottom: 50px;
  width: 100%;
  overflow-x: hidden;
  max-width: 900px;
}

.card {
  width: 100%;
  height: 325px;
  margin-bottom: 50px;
}
.card img {
  max-height: 160px;
  top: 0;
  width: 100%;
  border-radius: 5px;
  position: absolute;
  opacity: 0.9;
}
.card-content {
  background-color: var(--color-white);
  background-color: rgba(34, 34, 34, 0.8);
  border: 1px solid var(--color-light-dark);
  position: relative;
  height: 100%;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 5px;
  border: 1px solid var(--color-secondary);
}
.card .card-content div {
  text-align: center;
}
.card .card-content .go-to-project-page {
  text-align: center;
  margin-top: 10px;
}

.card .card-content a {
  text-decoration: none;
  color: var(--color-secondary);
  text-align: center;
  transition: 300ms;
  padding: 3px;
}
.card .card-content a:hover {
  background-color: var(--color-main);
  transition: 300ms;
  color: var(--color-white);
}
.card h1 {
  font-size: 25px;
  color: var(--color-black);
  text-align: center;
  transition: 300ms;
  margin: 0;
  margin-top: 165px;

  padding: 0;
}
.card h1:hover {
  color: var(--color-main);
  transition: 300ms;
}
.card p {
  padding-left: 15px;
  padding-right: 15px;
  max-height: 55px;
  overflow: hidden;
  font-family: Helvetica, sans-serif;
}

.card-technologies-used {
}
.card-technologies-used i {
  margin: 10px;
  color: var(--color-light-dark);
}

@media (min-width: 600px) {
  .project-section-bg-img {
    display: block;
  }
  .projects-heading {
    font-size: 4rem;
  }
  .projects-heading::after {
    width: 20%;
  }
  .card {
    max-width: 300px;
    margin: 0;
    border: solid 1px var(--color-light-dark);
  }
  .card-content {
    margin: 0;
    border-radius: 0;
  }
  .card img {
    margin: 0;
    border-radius: 0;
  }
}
